import React, { useContext, useState } from 'react'
import type { FormProps } from 'antd';
import { Button, Checkbox, Form, Input } from 'antd';
import './Auth.css';
import { Context } from '../../components/Context/AppContext';
import { start } from 'repl';

type FieldType = {
  username?: string;
  password?: string;
  remember?: string;
};

const onFinish: FormProps<FieldType>['onFinish'] = (values) => {
  console.log('Success:', values);
};

const onFinishFailed: FormProps<FieldType>['onFinishFailed'] = (errorInfo) => {
  console.log('Failed:', errorInfo);
};


export default function Auth() {
  const { isAuth, setAuth } = useContext(Context);

  const [isAuthForm, setIsAuthForm ] = useState(false);

  const login = () => {
    setAuth(true)
    localStorage.setItem('auth', 'true');
  };
  return (
    <div className='form_conteiner'>
      <Form
        className='form'
        name="basic"
        labelCol={{ span: 6 }}
        style={{ maxWidth: 500 }}
        initialValues={{ remember: true }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        autoComplete="off"
      >
        <Form.Item<FieldType>
          label=""
          name="username"
          rules={[{ required: true, message: ' ' }]}
        >
          <Input />
        </Form.Item>


        {isAuthForm ? <></> :
          <Form.Item<FieldType>
            label=""
            name="username"
            rules={[{ required: true, message: ' ' }]}
          >
            <Input />
          </Form.Item>
        }



        <Form.Item<FieldType>

          label=""
          name="password"
          rules={[{ required: true, message: ' ' }]}
        >
          <Input.Password />
        </Form.Item>


        {isAuthForm ? <></> :
          <Form.Item<FieldType>
            className="form-item-label"
            label=" "
            name="password"
            rules={[{ required: true, message: ' ' }]}

          >
            <Input.Password />
          </Form.Item>
        }

        <Form.Item<FieldType>
          name="remember"
          valuePropName="checked"
          style={{ alignSelf: 'center' }}
        >
          <Checkbox> </Checkbox>
        </Form.Item>

        <Form.Item style={{ alignSelf: 'center' }}>
          <Button type="primary" htmlType="submit">
            {isAuthForm ? <span onClick={() => login()}></span> : 
            <span onClick={() => login()}></span>}
          </Button>
        </Form.Item>

        {/*   ,       ,      */}
        <Form.Item style={{ alignSelf: 'center' }}>
          {isAuthForm ?
            <div >
               ? <span style={{ color: 'blue', cursor: 'pointer' }} onClick={() => setIsAuthForm(!isAuthForm)}></span>
            </div>
            :
            <div>
               ? <span style={{ color: 'blue', cursor: 'pointer' }} onClick={() => setIsAuthForm(!isAuthForm)}></span>
            </div>
          }
        </Form.Item>

      </Form>
    </div>
  )
}
